
@import "mixins";

body {
	font-family: "OpenSans", sans-serif;
	background: $grey;
	min-width: 835px;
	margin: 20px auto; }

h2 {
	text-transform: uppercase;
	font-size: 24px;
	font-weight: bold; }

.dib {
	display: inline-block; }

.mobile {
	display: none; }

.desktop {
	margin-bottom: 0; }

section {
	width: 100%;
	max-width: 835px;
	margin: auto; }

.info {
	width: 45%;
	position: relative; }

.image {
	width: 45%;
	height: 420px;
	float: left;
	margin-right: 40px;
	background: url('/img/lightbringer-trans.png') no-repeat 50% 50%;
	background-size: cover; }
.id {
	opacity: 0.7;
	font-size: 10px;
	color: $greyblue; }

.price {
	color: $crimson;
	font-weight: bold;
	font-size: 24px;
	span {
		color: $darkviolet;
		text-decoration: line-through; } }

.rating {
	margin: 25px 0;
	.star {
		display: none; }

	.star-custom {
		margin: 10px 8px;
		position: relative;
		display: inline-block;
		vertical-align: middle;
		cursor: pointer; }

	.star-custom:before {
		content: url(/img/star.png);
		position: absolute;
		@include transition-ease(0.5s);
		@include translate(-50%, -50%); }

	.star:checked + .star-custom::before {
		content: url(/img/star-colored.png);
		text-align: center;
		display: block;
		@include translate(-50%, -50%); } }



.share {
	color: $blue;
	font-weight: bold;
	font-size: 18px;
	cursor: pointer;
	margin-left: 20px; }

.share:hover .toggle {
	display: block;
	@include transition-ease(0.5s); }

.share-with:after {
	content: '';
	position: absolute;
	left: 50%;
	top: -20px; }

.toggle {
	color: $blue;
	background: url('/img/background-share-it.png') no-repeat;
	background-size: contain;
	padding: 15px 10px 20px;
	@include transition-ease(0.5s);
	display: none;
	position: absolute;
	top: 140px;
	left: 0;
	cursor: default;
	.share-h {
		font-weight: bold;
		margin-top: 7px;
		margin-left: 5px; }

	.close {
		width: 15px;
		height: 15px;
		position: absolute;
		top: 25px;
		right: 15px;
		cursor: pointer; }

	.close:before, .close:after {
		position: absolute;
		left: 7px;
		content: ' ';
		height: 15px;
		width: 2px;
		background-color: $blue;
		@include transition-ease(0.5s); }

	.close:before {
		transform: rotate(45deg); }

	.close:after {
		transform: rotate(-45deg); }

	.close:hover:before, .close:hover:after {
		background:  darken($blue, 30%); }

	.social {
		display: inline-block;
		border-radius: 30px;
		border: 2px solid $blue;
		margin: 20px 5px;
		width: 40px;
		height: 40px;
		@include transition-ease(0.5s);
		text-align: center;
		cursor: pointer;
		i {
			font-size: 16px;
			position: relative;
			top: 50%;
			@include transition-ease(0.5s);
			@include translate(0, -60%); } }
	:hover i {
		color: darken($blue, 30%); }
	:hover {
		border-color: darken($blue, 30%); } }

.details {
	font-size: 12px;
	color: $details;
	margin: 0 0 30px; }

.size {
	text-transform: uppercase;
	font-weight: bold;
	margin-bottom: 20px;

	.checkbox {
		display: none; }

	.checkbox-custom {
		width: 40px;
		height: 40px;
		margin: 10px 5px;
		border: 1px solid $black;
		border-radius: 30px;
		background: $white;
		position: relative;
		display: inline-block;
		vertical-align: middle;
		cursor: pointer; }

	.checkbox-custom:before {
		position: absolute;
		top: 50%;
		left: 50%;
		@include transition-ease(0.5s);
		@include translate(-50%, -50%); }

	.xs:before {
		content: 'xs'; }

	.s:before {
		content: 's'; }

	.m:before {
		content: 'm'; }

	.l:before {
		content: 'l'; }

	.checkbox:checked + .checkbox-custom::before {
		text-align: center;
		line-height: 40px;
		width: 40px;
		height: 40px;
		display: block;
		color: $white;
		position: absolute;
		top: 50%;
		left: 50%;
		@include translate(-50%, -50%);
		background: #413548;
		border-radius: 30px; }

	.size-caption:hover,
	.size-caption:focus,
	.size-caption:active {
		background: $size-hover;
		div {
			color: $white; } } }

input[type="submit"] {
	color: $darkviolet;
	background: $oceanblue;
	border: none;
	text-transform: uppercase;
	font-weight: bold;
	padding: 15px 45px;
	border-radius: 30px;
	font-size: 18px;
	cursor: pointer;
	outline: none;
	@include transition-ease(0.5s); }

input[type="submit"]:hover {
	background: darken($oceanblue, 15%); }

input[type="submit"]:active,
input[type="submit"]:focus {
	background: darken($oceanblue, 30%); }

.grid {
	margin-top: 60px;
	#slider {
		input {
			display: none; } }

	.inner {
		font-size: 0;
		line-height: 0;
		letter-spacing: -1px; }
	.product {
		background: $black;
		width: 265px;
		height: 265px;
		display: inline-block;
		margin: 0 20px 10px 0;
		position: relative; }
	.product:nth-child(3),
	.product:nth-child(6) {
		margin-right: 0; }

	.product-1 {
		background: url('/img/product-1.png') no-repeat 50% 50%;
		background-size: cover; }
	.product-2 {
		background: url('/img/product-2.png') no-repeat 50% 50%;
		background-size: cover; }
	.product-3 {
		background: url('/img/product-3.png') no-repeat 50% 50%;
		background-size: cover; }
	.product-4 {
		background: url('/img/product-4.png') no-repeat 50% 50%;
		background-size: cover; }
	.product-5 {
		background: url('/img/product-5.png') no-repeat 50% 50%;
		background-size: cover; }
	.product-6 {
		background: url('/img/product-6.png') no-repeat 50% 50%;
		background-size: cover; }

	.price {
		display: inline-block;
		padding: 10px;
		font-size: 16px;
		position: absolute;
		right: 0;
		bottom: 20px;
		span {
			position: relative; }
		.colored {
			text-decoration: none;
			color: $crimson; } }

	.price:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 1;
		background: $oceanblue; } }









